import {getOwnProfiles} from '@/api/profile'

export default {
  name: "profileIndex",
  middleware: 'authenticated',
  async asyncData({params}) {
    const {data} = await getOwnProfiles(params.username)
    return {
      profile: data.profile
    }
  }
}
